import React from "react";
import s from './Post.module.css';

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src="https://tvbesedka.com/upload/2018/8/17/0/14/28/32634856-ec7c-4ef2-9d04-c1a2325dac76.jpg" alt=""/>
            {props.message}
            <div>
                <span className={s.like}> Likes </span> {props.likeCount}
            </div>
        </div>
    );
}
export default Post;